const SUBSTITUTE_REG = /\\?\$\{([^{}]+)\}/g,
    EMPTY = ''

let StringHelper = {
    /**
     * Substitutes keywords in a string using an object/array.
     * Removes undefined keywords and ignores escaped keywords.
     * 
     * str = '{name} is {prop_1} and {prop_2}.',
     * obj = {name: 'Jack Bauer', prop_1: 'our lord', prop_2: 'savior'};
     * S.substitute(str, obj); // => 'Jack Bauer is our lord and savior.'
     * 
     * @param {String} str template string
     * @param {Object} o json data
     * @member KISSY
     * @param {RegExp} [regexp] to match a piece of template string
     */
    substitute: function (str, o, regexp) {
        if (typeof str !== 'string' || !o) {
            return str;
        }

        return str.replace(regexp || SUBSTITUTE_REG, function (match, name) {
            if (match.charAt(0) === '\\') {
                return match.slice(1);
            }
            return (o[name] === undefined) ? EMPTY : o[name];
        });
    }
}

StringHelper.trim = function(str) {
    return str.replace(/(?:^[ \t\n\r]+)|(?:[ \t\n\r]+$)/g, '');
}
/**
 * 
 * @param  {[type]} string [description]
 * @return {[type]}        [description]
 */
StringHelper.toCamelCase = function (string) {
    return string.toLowerCase().replace(/-(.)/g, function(match, group1) {
        return group1.toUpperCase();
    });
};





/**
 * UTF-8UTF-16
 * UTF-8  Unicode 
 *
 * 000000 - 00007F(128)      0zzzzzzz(00-7F)                             
 * 000080 - 0007FF(1920)     110yyyyy(C0-DF) 10zzzzzz(80-BF)             
 * 000800 - 00D7FF
   00E000 - 00FFFF(61440)    1110xxxx(E0-EF) 10yyyyyy 10zzzzzz           
 * 010000 - 10FFFF(1048576)  11110www(F0-F7) 10xxxxxx 10yyyyyy 10zzzzzz  
 *
 * : Unicode D800-DFFF 
 * {@link http://zh.wikipedia.org/wiki/UTF-8}
 *
 * UTF-16  65535 
 * 000000 - 00FFFF  
 * 010000 - 10FFFF  
 *
 * {@link http://zh.wikipedia.org/wiki/UTF-16}
 * @param  {String} str
 * @param  {String} charset utf-8, utf-16
 * @return {Number}
 */
StringHelper.sizeof = function(str, charset){
    var total = 0,
        charCode,
        i,
        len;
    charset = charset ? charset.toLowerCase() : '';
    if(charset === 'utf-16' || charset === 'utf16'){
        for(i = 0, len = str.length; i < len; i++){
            charCode = str.charCodeAt(i);
            if(charCode <= 0xffff){
                total += 2;
            }else{
                total += 4;
            }
        }
    }else{
        for(i = 0, len = str.length; i < len; i++){
            charCode = str.charCodeAt(i);
            if(charCode <= 0x007f) {
                total += 1;
            }else if(charCode <= 0x07ff){
                total += 2;
            }else if(charCode <= 0xffff){
                total += 3;
            }else{
                total += 4;
            }
        }
    }
    return total;
}

export default StringHelper


